mergeI18nTranslations(JSON.parse("{\"ar\":{\"would_you_like_to_let_students_continue_in_the_cou_a4c36171\":\"هل ترغب في ترك الطلاب متابعة المساق أم ترغب في إعادة قفل هذه الوحدات حتى يتم إكمال المتطلبات الجديدة؟\",\"you_have_changed_the_progression_requirements_for__6772bc89\":\"لقد قمت بتغيير متطلبات التقدم لمساق نشط. من المحتمل وجود طلاب تقدموا بالفعل إلى هذه الوحدة وإلى وحدات أخرى تعتمد عليها.\"},\"cy\":{\"would_you_like_to_let_students_continue_in_the_cou_a4c36171\":\"Ydych chi am adael i fyfyrwyr fwrw ymlaen â'r cwrs, neu ydych chi am gloi’r modiwlau hyn eto nes bydd y gofynion newydd wedi’u bodloni?\",\"you_have_changed_the_progression_requirements_for__6772bc89\":\"Rydych chi wedi newid gofynion cynnydd ar gyfer cwrs gweithredol. Mae’n bosib bod rhai myfyrwyr wedi symud ymlaen i’r modiwl hwn yn barod, ac i eraill sy’n dibynnu arno.\"},\"da\":{\"would_you_like_to_let_students_continue_in_the_cou_a4c36171\":\"Vil du lade studerende fortsætte i faget, eller vil du låse disse moduler igen, indtil de nye krav opfyldes?\",\"you_have_changed_the_progression_requirements_for__6772bc89\":\"Du har ændret udviklingskravet for et aktivt fag. Der findes muligvis studerende, som allerede er nået frem til dette modul og til andre, som afhænger heraf.\"},\"da-x-k12\":{\"would_you_like_to_let_students_continue_in_the_cou_a4c36171\":\"Vil du lade eleverne fortsætte i faget, eller vil du låse disse forløb igen, indtil de nye krav opfyldes?\",\"you_have_changed_the_progression_requirements_for__6772bc89\":\"Du har ændret udviklingskravet for et aktivt fag. Der findes muligvis elver, som allerede er nået frem til dette forløb og til andre, som afhænger heraf.\"},\"de\":{\"would_you_like_to_let_students_continue_in_the_cou_a4c36171\":\"Möchten Sie den Studenten gestatten, mit dem Kurs fortzufahren, oder möchten Sie diese Module erneut sperren, bis die neuen Anforderungen erfüllt sind?\",\"you_have_changed_the_progression_requirements_for__6772bc89\":\"Sie haben die Fortschrittsanforderungen für einen aktiven Kurs geändert. Möglicherweise sind Studenten bereits bei diesem Modul angelangt, sowie bei anderen, die davon abhängen.\"},\"el\":{\"would_you_like_to_let_students_continue_in_the_cou_a4c36171\":\"Θα θέλατε να επιτρέψετε στους σπουδαστές να συνεχίσουν στο μάθημα ή θέλετε να ξανακλειδώσετε αυτές τις ενότητες μέχρι να καλυφθούν οι νέες απαιτήσεις;\",\"you_have_changed_the_progression_requirements_for__6772bc89\":\"\\u003cmrk mid=\\\"7859\\\" mtype=\\\"seg\\\"\\u003eΈχετε αλλάξει τις απαιτήσεις προόδου για κάποιο ενεργό μάθημα.\\u003c/mrk\\u003e \\u003cmrk mid=\\\"7860\\\" mtype=\\\"seg\\\"\\u003eΕνδέχεται να υπάρχουν σπουδαστές που έχουν ήδη σημειώσει πρόοδο σε αυτή την ενότητα και σε άλλες και εξαρτώνται από αυτό.\\u003c/mrk\\u003e\"},\"en-AU\":{\"would_you_like_to_let_students_continue_in_the_cou_a4c36171\":\"Would you like to let students continue in the course or do you want to re-lock these modules until the new requirements are completed?\",\"you_have_changed_the_progression_requirements_for__6772bc89\":\"You have changed the progression requirements for an active course. There may be students who have already progressed to this module and to others that depend on it.\"},\"en-CA\":{\"would_you_like_to_let_students_continue_in_the_cou_a4c36171\":\"Would you like to let students continue in the course or do you want to re-lock these modules until the new requirements are completed?\",\"you_have_changed_the_progression_requirements_for__6772bc89\":\"You have changed the progression requirements for an active course. There may be students who have already progressed to this module and to others that depend on it.\"},\"en-GB\":{\"would_you_like_to_let_students_continue_in_the_cou_a4c36171\":\"Would you like to let students continue in the course or do you want to re-lock these modules until the new requirements are completed?\",\"you_have_changed_the_progression_requirements_for__6772bc89\":\"You have changed the progression requirements for an active course. There may be students who have already progressed to this module and to others that depend on it.\"},\"es\":{\"would_you_like_to_let_students_continue_in_the_cou_a4c36171\":\"¿Le gustaría permitir a los estudiantes que continuar en el curso o desea bloquear de nuevo los módulos hasta que se completen los nuevos requisitos?\",\"you_have_changed_the_progression_requirements_for__6772bc89\":\"Ha modificado los requisitos de progresión de un curso activo. Es posible que haya estudiantes que ya hayan pasado este módulo y otros que aún dependan de él.\"},\"fa\":{\"would_you_like_to_let_students_continue_in_the_cou_a4c36171\":\"مایلید به دانشجویان اجازه دهید به درس ادامه دهند یا می خواهید این ماژول ها را تا زمان تکمیل شدن الزامات جدید دوباره قفل کنید؟\",\"you_have_changed_the_progression_requirements_for__6772bc89\":\"الزامات پیشرفت یک درس فعال را تغییر داده اید. ممکن است دانشجویانی باشند که به این ماژول یا سایر ماژول هایی که به آن وابسته اند پیشرفت کرده باشند.\"},\"fi\":{\"would_you_like_to_let_students_continue_in_the_cou_a4c36171\":\"Haluatko antaa opiskelijoiden jatkavan kurssilla vai haluatko poistaa näiden moduulien lukituksen, kunnes uudet vaatimukset täyttyvät?\",\"you_have_changed_the_progression_requirements_for__6772bc89\":\"Olet muuttanut edistymisvaatimuksia aktiiviselle kurssille. Osa opiskelijoista on ehkä edennyt tähän moduuliin ja muihin sellaisiin, jotka riippuvat siitä.\"},\"fr\":{\"would_you_like_to_let_students_continue_in_the_cou_a4c36171\":\"Voulez-vous laisser les étudiants continuer le cours ou préférez-vous verrouiller à nouveau ces modules jusqu’à ce que les nouvelles conditions soient remplies ?\",\"you_have_changed_the_progression_requirements_for__6772bc89\":\"Vous avez changé les critères de progression pour un cours actif. Des étudiants ont peut-être déjà commencé ce module ou avancé dans d’autres qui dépendent de celui-ci.\"},\"fr-CA\":{\"would_you_like_to_let_students_continue_in_the_cou_a4c36171\":\"Voulez-vous laisser les étudiants continuer le cours ou préférez-vous verrouiller à nouveau ces modules jusqu’à ce que les nouvelles conditions soient remplies?\",\"you_have_changed_the_progression_requirements_for__6772bc89\":\"Vous avez changé les critères de progression pour un cours actif. Des étudiants ont peut-être déjà commencé ce module ou avancé dans d’autres qui dépendent de celui-ci.\"},\"he\":{\"would_you_like_to_let_students_continue_in_the_cou_a4c36171\":\"האם ברצונך לאפשר לתלמידים להמשיך בקורס או ברצונך לנעול מחדש מודולים אלו עד אשר הדרישות החדשות ימולאו?\",\"you_have_changed_the_progression_requirements_for__6772bc89\":\"שינית את דרישות ההתקדמות בקורס פעיל. יתכן שיש תלמידים שכבר הגיעו למודול זה ואחרים שתלויים בו.\"},\"ht\":{\"would_you_like_to_let_students_continue_in_the_cou_a4c36171\":\"Èske w ta renmen kite elèv yo kontinye nan kou a oswa ou vle rebloke modil sa yo jiskaske nouvo egzijans yo konplete?\",\"you_have_changed_the_progression_requirements_for__6772bc89\":\"Ou chanje egzijans pwogresyon yon kou ki aktif. Li posib pou gen elèv ki gentan fè pwogrè nan modil sa a e nan lòt ki depann de li.\"},\"hu\":{\"would_you_like_to_let_students_continue_in_the_cou_a4c36171\":\"Szeretné, hogy a hallgatók folytathassák a kurzust, vagy újra lezárja a modult, amíg az új követelmények teljesülnek?\",\"you_have_changed_the_progression_requirements_for__6772bc89\":\"Megváltoztatta egy aktív kurzushoz tartozó haladási követelményeket. Lehetnek hallgatók, akik már eljutottak ehhez a modulhoz, vagy másokhoz, amik ettől függenek.\"},\"hy\":{\"would_you_like_to_let_students_continue_in_the_cou_a4c36171\":\"Կցանկանայի՞ք թույլ տալ ունկնդիրներին շարունակել դասընթացը, թե՞ ցանկանում եք կրկին արգելափակել մոդուլները մինչև նոր պահանջների կատարումը:\",\"you_have_changed_the_progression_requirements_for__6772bc89\":\"Դուք փոխել եք ակտիվ դասընթացում առաջ ընթանալու պահանջները: Կարող են լինել ունկնդիրներ, ովքեր արդեն անցել են այս մոդուլին և այլ մոդուլներին, որ կախված են դրանից:\"},\"is\":{\"would_you_like_to_let_students_continue_in_the_cou_a4c36171\":\"Viltu láta nemendur halda áfram í námskeiðinu eða viltu endurlæsa þessum einingum þar til nýju kröfunum er lokið?\",\"you_have_changed_the_progression_requirements_for__6772bc89\":\"Þú hefur breytt framvindukröfum fyrir virkt námskeið. Mögulega eru nemendur sem hafa þegar náð þessari einingu og aðrir sem treysta á það.\"},\"it\":{\"would_you_like_to_let_students_continue_in_the_cou_a4c36171\":\"Desideri consentire ai tuoi studenti di continuare nel corso o vuoi bloccare di nuovo questi moduli fino al completamento dei nuovi requisiti?\",\"you_have_changed_the_progression_requirements_for__6772bc89\":\"Hai modificato i requisti di progresso per un corso attivo. Possono essere presenti studenti che sono già avanzati in questo modulo e altri che dipendono da esso.\"},\"ja\":{\"would_you_like_to_let_students_continue_in_the_cou_a4c36171\":\"受講生がコースを続行できるようにしますか? または、新しい要件が完了するまで、これらのモジュールを再度ロックしますか?\",\"you_have_changed_the_progression_requirements_for__6772bc89\":\"アクティブなコースの進捗要件を変更しました。このモジュールをすでに進めている受講生、またはこのモジュールに依存している他のモジュールを進めている受講生がいる可能性があります。\"},\"mi\":{\"would_you_like_to_let_students_continue_in_the_cou_a4c36171\":\"E hiahia ana koe ki te tuku tonu ngā ākonga i roto i te akoranga e hiahia ana koe ki te anō-rakaina enei ngā kōwae kia oti te whakaritenga hou rānei?\",\"you_have_changed_the_progression_requirements_for__6772bc89\":\"Kua hurihia e koe te whakaritenga haerere mo te akoranga hohe. He kia ākonga nei i ahu whakamua ki tēnei kōwae, me te ki ētahi atu e whakawhirinaki i runga i te reira kua.\"},\"nb\":{\"would_you_like_to_let_students_continue_in_the_cou_a4c36171\":\"Har du lyst til å la studenter fortsette i emnet eller ønsker du å låse disse modulene igjen til de nye kravene er fullført?\",\"you_have_changed_the_progression_requirements_for__6772bc89\":\"Du har endret progresjonskrav for et aktivt emne. Det kan være studenter som allerede har kommet til denne modulen, og til andre som er avhengige av den.\"},\"nb-x-k12\":{\"would_you_like_to_let_students_continue_in_the_cou_a4c36171\":\"Har du lyst til å la elever fortsette i faget eller ønsker du å låse disse moduler igjen til de nye kravene er fullført?\",\"you_have_changed_the_progression_requirements_for__6772bc89\":\"Du har endret progresjonskrav for et aktivt fag. Det kan være elever som allerede har kommet til denne modul, og til andre som er avhengige av den.\"},\"nl\":{\"would_you_like_to_let_students_continue_in_the_cou_a4c36171\":\"Wil je cursisten door laten gaan met de cursus, of wil je deze modules opnieuw vergrendelen tot de nieuwe vereisten voltooid zijn?\",\"you_have_changed_the_progression_requirements_for__6772bc89\":\"Je hebt de voortgangsvereisten voor een actieve cursus veranderd. Er kunnen cursisten zijn die al naar deze module door zijn gegaan en anderen die ervan afhankelijk zijn.\"},\"nn\":{\"would_you_like_to_let_students_continue_in_the_cou_a4c36171\":\"Vil du at studentane skal halde fram på emnet eller vil du låse modulane på nytt til nye krav er fullførte?\",\"you_have_changed_the_progression_requirements_for__6772bc89\":\"Du har endra krav for progresjon for eit aktivt emne. Det kan vere studentar som allereie har kome til denne modulen og andre som reknar med å kome hit.\"},\"pl\":{\"would_you_like_to_let_students_continue_in_the_cou_a4c36171\":\"Czy chcesz pozwolić uczestnikom kontynuować udział w kursie, czy może chcesz ponownie zablokować te moduły do czasu ukończenia nowych wymagań?\",\"you_have_changed_the_progression_requirements_for__6772bc89\":\"Zmieniono wymagania postępu dotyczące aktywnego kursu. Być może są uczestnicy, którzy już przeszli do tego modułu i do innych, które są z nim powiązane.\"},\"pt\":{\"would_you_like_to_let_students_continue_in_the_cou_a4c36171\":\"Pretende deixar que os alunos continuem na disciplina ou deseja voltar a bloquear estes módulos até os novos requisitos serem concluídos?\",\"you_have_changed_the_progression_requirements_for__6772bc89\":\"Alterou os requisitos de progressão relativos a uma disciplina ativa. É possível que haja alunos que já avançaram para este módulo e para outros que dependem dele.\"},\"pt-BR\":{\"would_you_like_to_let_students_continue_in_the_cou_a4c36171\":\"Gostaria de deixar os alunos continuarem no curso ou deseja voltar a bloquear esses módulos até que os novos requisitos sejam concluídos?\",\"you_have_changed_the_progression_requirements_for__6772bc89\":\"Você alterou os requisitos de progressão de um curso ativo. Pode haver alunos que já evoluíram para este módulo e outros que dependem dele.\"},\"ru\":{\"would_you_like_to_let_students_continue_in_the_cou_a4c36171\":\"Вы хотите позволить студентам продолжить обучение на курсе или вы хотите повторно заблокировать эти модули до завершения новых требований?\",\"you_have_changed_the_progression_requirements_for__6772bc89\":\"Вы изменили требования к прохождению для активного курса. На курсе могут быть студенты, которые уже продвинулись до этого модуля или других, связанных с ним.\"},\"sl\":{\"would_you_like_to_let_students_continue_in_the_cou_a4c36171\":\"Ali želite, da študenti nadaljujejo s predmetom ali pa morda želite znova zakleniti te module, dokler nove zahteve niso zaključene?\",\"you_have_changed_the_progression_requirements_for__6772bc89\":\"Spremenili ste zahteve napredovanja za aktivni predmet. Morda obstajajo študenti, ki so že napredovali v ta modul in v druge, od njega odvisne module.\"},\"sv\":{\"would_you_like_to_let_students_continue_in_the_cou_a4c36171\":\"Vill du låta studenter fortsätta i kursen eller vill du låsa de här modulerna igen tills de nya kraven har uppfyllts?\",\"you_have_changed_the_progression_requirements_for__6772bc89\":\"Du har ändrat utvecklingskraven för en aktiv kurs. Det kan finnas studenter som redan har gått vidare till den här modulen och andra som är beroende av den.\"},\"sv-x-k12\":{\"would_you_like_to_let_students_continue_in_the_cou_a4c36171\":\"Vill du låta elever fortsätta i kursen eller vill du låsa de här modulerna igen tills de nya kraven har uppfyllts?\",\"you_have_changed_the_progression_requirements_for__6772bc89\":\"Du har ändrat utvecklingskraven för en aktiv kurs. Det kan finnas elever som redan har gått vidare till den här modulen och andra som är beroende av den.\"},\"tr\":{\"would_you_like_to_let_students_continue_in_the_cou_a4c36171\":\"Öğrenciler derse devam etsinler mi yoksa yeni gereksinimler tamamlanana kadar bu modülleri yeniden kilitlemek mi istersiniz?\",\"you_have_changed_the_progression_requirements_for__6772bc89\":\"Etkin bir derste ilerleme gereksinimlerini değiştirdiniz. Bu modüle ya da bu modüle bağlı diğer modüllere kadar ilerlemiş öğrenciler olabilir.\"},\"uk\":{\"would_you_like_to_let_students_continue_in_the_cou_a4c36171\":\"Хотіли б ви дозволити студентам продовжувати курс, чи хочете заблокувати ці модулі до виконання нових вимог?\",\"you_have_changed_the_progression_requirements_for__6772bc89\":\"Ви змінили вимоги щодо прогресу для активного курсу. Там можуть бути студенти, які вже просунулися до цього модуля та до інших, які залежать від нього.\"},\"zh-Hans\":{\"would_you_like_to_let_students_continue_in_the_cou_a4c36171\":\"您要让学生继续学习该课程，还是希望重新锁定这些单元，直到新要求设定完毕？\",\"you_have_changed_the_progression_requirements_for__6772bc89\":\"您已更改某个活动课程的进度要求。可能有些学生的进度已达到此单元以及依赖此单元的其他单元。\"},\"zh-Hant\":{\"would_you_like_to_let_students_continue_in_the_cou_a4c36171\":\"您希望讓學生繼續學習該課程，還是希望重新鎖定這些單元，直至滿足新要求？\",\"you_have_changed_the_progression_requirements_for__6772bc89\":\"您已更改某一活動課程的進度要求。可能有些學生的進度已達到此單元、以及基於單元的其他單元的進度要求。\"}}"));
